// Container Properties

.container {
  width: 100%;
  max-width: var(--op-breakpoint-medium);
  padding: 0 var(--op-space-large);
  margin: 0 auto;
}

.container--sm {
  max-width: var(--op-breakpoint-small);
}

.container--md-padding {
  padding: 0 var(--op-space-medium);
}

.container--sm-padding {
  padding: 0 var(--op-space-small);
}

.container--xs {
  max-width: var(--op-breakpoint-x-small);
}

// Width Properties

.full-width {
  width: 100%;
}

.half-width {
  width: 50%;
}

.four-fifths-width {
  width: calc(100% * 4 / 5);
}

.three-fifths-width {
  width: calc(100% * 3 / 5);
}

.two-fifths-width {
  width: calc(100% * 2 / 5);
}

.one-fifth-width {
  width: calc(100% * 1 / 5);
}

.three-quarters-width {
  width: calc(100% * 3 / 4);
}

.one-quarter-width {
  width: calc(100% * 1 / 4);
}

.two-thirds-width {
  width: calc(100% * 2 / 3);
}

.one-third-width {
  width: calc(100% * 1 / 3);
}

