// Layout properties
// Layout row evenly spaces all elements within it.
// It also makes it responsive at the sm breakpoint.
// If you combine it with flex-wrap, it tries to break using the
// normal flex wrap rules instead of responsively.

// E.G:
// .flex.layout-row
//   .card Item
//   .card Item
//   .card Item

// Normal width
// | Item | | Item | | Item |
// Mobile
// | Item |
// | Item |
// | Item |

// E.G:
// .flex.layout-row.flex-wrap
//   .card Item
//   .card Item

// Normal width
// | Item | | Item | | Item |
// Mobile (really small)
// | Item | | Item |
// |      Item     |
.layout-row {
  & > * {
    flex: 1;
  }

  @media screen and (max-width: $breakpoint-small) {
    // Use flex wrapping strategy instead of responsive wrapping at sm breakpoint.
    &:not(.flex-wrap) {
      flex-direction: column;
    }
  }
}

// Flexible Layout
// This allows for creating a grid like area using one flex container with multiple lines
// Percentage based widths can be used to define the areas of each row.
// Note: This responsively breaks at $breakpoint-small to a single column.
// You can add the .flex-no-wrap class to prevent that.
// This can also be combined with .gap-{size} to space items without having
// them wrap when you don't want them to.

// E.G
// | One Fifth | | One Fifth | | One Fifth | | One Fifth | | One Fifth |
// | One Quarter  | | One Quarter  | |  One Quarter  | |  One Quarter  |
// |      One Third      | |      One Third      | |     One Third     |
// |            One Half            | |            One Half            |
// |                              One Full                             |
// Mobile
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |
// | Item |

// .flexible-layout-area can be nested in the item of another .flexible-layout-area
// for a more complex layout if needed. Gaps are not inherited and will need
// to be redefined at each level

.flexible-layout-area {
  display: flex;
  flex-wrap: wrap;

  & > * {
    flex: 1;

    &.four-fifths-width {
      flex-basis: calc(calc(100% * 4 / 5) - var(--op-gap));
    }

    &.three-fifths-width {
      flex-basis: calc(calc(100% * 3 / 5) - var(--op-gap));
    }

    &.two-fifths-width {
      flex-basis: calc(calc(100% * 2 / 5) - var(--op-gap));
    }

    &.one-fifth-width {
      flex-basis: calc(calc(100% * 1 / 5) - var(--op-gap));
    }

    &.three-quarters-width {
      flex-basis: calc(calc(100% * 3 / 4) - var(--op-gap));
    }

    &.one-quarter-width {
      flex-basis: calc(calc(100% * 1 / 4) - var(--op-gap));
    }

    &.two-thirds-width {
      flex-basis: calc(calc(100% * 2 / 3) - var(--op-gap));
    }

    &.one-third-width {
      flex-basis: calc(calc(100% * 1 / 3) - var(--op-gap));
    }

    &.full-width {
      flex-basis: calc(100% - var(--op-gap));
    }

    &.half-width {
      flex-basis: calc(50% - var(--op-gap));
    }
  }

  @media screen and (max-width: $breakpoint-small) {
    &:not(.flex-no-wrap) {
      flex-direction: column;

      & > * {
        width: 100%;
        flex-basis: 100%;
      }
    }
  }
}

// Accessibility
// Use the following class to hide accessibility text that is needed for screenreaders but needs to be hidden from regular users.
// https: //snook.ca/archives/html_and_css/hiding-content-for-accessibility
.sr-only {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}
